import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import './ReservationDiv.css';


export function ReservationDiv({reservation, blockExpanded = false}) {
    let {
        id,
        time,
        flyer,
        flyerData,
        timeVal,
        tarifName,
        paymentData,
        notes,
    } = reservation;

    const [expanded, setExpanded] = useState(blockExpanded);

    useEffect(() => {
        setExpanded(blockExpanded)
    }, [blockExpanded]);

    const onClick = () => {setExpanded(!expanded)}

    return (<>
        <div
            className="reservation"
            onClick = {onClick}
        >
            {/* {JSON.stringify(reservation)} */}
            <span>{timeVal}</span>
            <span>{tarifName}</span>
            <span>{flyer}</span>
            <span>{notes}</span>
            <span>{id}</span>
            <span>{paymentData}</span>
            <span>{flyerData}</span>
            { expanded &&
                <div className="reservation-detailed">
                    <pre>additional data  </pre>
                    <pre>additional data  </pre>
                    <pre>additional data  </pre>
                </div>
            }
        </div>

    </>)
}