import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import './DayBlockDiv.css';


import { _Date, TIME_SLOTS } from '../../../helpers/_Date';
import { TimeSlotDiv } from '../TimeSlotDiv/TimeSlotDiv';


export function DayBlockDiv({dateTimeblocks}) {
    // console.log(dateTimeblocks)
    // console.log(Object.entries(dateTimeblocks))
    const TimeBlocks = Object.entries(dateTimeblocks).map(
        ([slotN, dateBlock]) => <TimeSlotDiv
            key={slotN}
            time={TIME_SLOTS[slotN][2]}
            reservationsArr={dateBlock}
        />

    )


    return (
        <div className="dayBlockDiv">
            {TimeBlocks}
        </div>
    )
};