const LOCAL_MOCK_API_DATA = MOCK_APIRESP.invoices// as Type_InvoiceApiData[];

const MOCK_PARCED_APIDATA = LOCAL_MOCK_API_DATA.map((rec) => parseRawSourseDataRec(rec));

function parseRawSourseDataRec(record) {
    return {
        No: record['id'] || '',
        Status: record['status']?.['name'] || '',

        'Date created': record['datetimeCreated'] || '',
        'Total amount': record['totalAmount'] || '',
        'E-mail': record['payerEmail'] || '',
        Name: record['payerName'] || '',
        Phone: record['payerPhone'] || '',
        Notes: record['notes'] || '',

        UTM: record['utmCampaign'] || '' + record['utmMedium'] || '' + record['utmSource'] || '',

        ...parseItem(record['items']?.[0]),
        ...parsePayment(record['payments']?.[0]),

        'User refunded': record['userRefunded'] || '',

        'Datetime refunded': record['datetimeRefunded'] || '',
        'Refund Reason': record['refundReason'] || '',
        'Refund notes': record['refundNotes'] || '',
    };
}

function parseItem(item) {
    return {
        'Item type': item?.object?.type || '',
        Tariff: item?.object?.tariff || '',
        Minutes: item?.object?.minutes?.toString() || '',
        Sets: item?.object?.people?.toString() || '',
        Timeblock: item?.object?.timeblock || '',

        'Full price': item?.amount?.toString() || '',

        Discount: '' + item?.discount?.toString() + ' ' + item?.discountNotes,

        Count: item?.count?.toString() || '',
        'Final price': item?.totalAmount?.toString() || '',
    };
}

function parsePayment(payment) {
    return {
        'Date paid': payment?.datetimePaid || '',

        'Payment system': payment?.system?.name || '',

        Amount: payment?.amountPaid?.toString() || '',

        POS: payment?.cashDrawer || '',

        'User created': payment?.userCreated?.username || '',
    };
}

const DATE_TYPE_MAP = {
    created: 'Date created',
    paid: 'Date paid',
    refunded: 'Datetime refunded',
}// as const;

const QUERY_FIELDS = ['E-mail', 'Name', 'Notes', 'UTM']// as const;

const STATUSES = ['Any', 'Paid', 'New', 'Expired']// as const;

const filterByDate = (
    initData,//: typeof MOCK_PARCED_APIDATA,
    dateType,//: keyof typeof DATE_TYPE_MAP,
    dateFr,//: string,
    dateTo,//: string,
) =>
    initData.filter((invoice) => {
        const dateTypeName = DATE_TYPE_MAP[dateType];
        const invoiceDateStr = invoice[dateTypeName]?.slice(0, 10) || '';
        const invoiceDate = Date.parse(invoiceDateStr);
        const requestDateFr = Date.parse(dateFr) || Date.now();
        const requestDateTo = Date.parse(dateTo) || Date.now();

        return invoiceDate >= requestDateFr && invoiceDate <= requestDateTo;
    });

const filterByStatus = (initData, statusiD) => {
    if (statusiD == '0') return initData;
    return initData.filter((invoice) => invoice.Status === STATUSES[statusiD]);
};

const filterByQuery = (initData, query) => {
    if (!query) return initData;
    return initData.filter((invoice) =>
        QUERY_FIELDS.some((field) => invoice[field]?.includes(query)),
    );
};

const composedFiler = (initData, requestData) => {
    const { status = '0', query = '', dateType, dateFr, dateTo } = requestData;

    const filteredByDate = filterByDate(initData, dateType, dateFr, dateTo);
    const filteredByStatus = filterByStatus(filteredByDate, status);
    const filteredByQuery = filterByQuery(filteredByStatus, query);

    return filteredByQuery;
};

function API_GET_INVOICES(requestData) {
    if (typeof requestData === 'object') {
        const initData = MOCK_PARCED_APIDATA;
        const filteredData = composedFiler(initData, requestData);
        return filteredData;
    }

    switch (requestData) {
        case 'start': {
            return MOCK_PARCED_APIDATA.slice(0, 3);
        }

        case 'rand': {
            const count = Math.floor(Math.random() * 7);
            const data = MOCK_PARCED_APIDATA.slice(count, count + 7);
            return data;
        }

        case 'all': {
            return MOCK_PARCED_APIDATA;
        }
    }

    console.log('API GI');
    return MOCK_PARCED_APIDATA;
}

function initDateTimeblocks() {
    const res = {}
    TIME_SLOTS.forEach(
        (slotN, i) => {res[i] = [] }
    )
    return res
}

export function API_GET_TIMEBLOCKS(invoices) {
    let TIMEBLOCKS_OBJ = {}
    invoices.forEach(reservation => {
        const time = new Date(reservation.Timeblock)
        const flightDateN = _Date.dateMsToDateN(time);
        const timeSlotN = _Date.msToSlotN(time);

        const timeVal       = +reservation.Minutes
        const flyer         = +reservation.Name
        const id            = reservation.id
        const tarifName     = reservation.Tariff
        const paymentData   = reservation.Status + reservation.Amount
        const flyerData     = reservation['E-mail']
        const notes         = reservation.Notes

        const additionalData = {
            'Date created'    : reservation['Date created']  ,
            'User created'    : reservation['User created']  ,
            'Discount'        : reservation['Discount']      ,
            'Payment system'  : reservation['Payment system'],
            'UTM'             : reservation['UTM']           ,
            'Date paid'       : reservation['Date paid']     ,
            'Phone'           : reservation['Phone']         ,
        }

        if (flightDateN && !TIMEBLOCKS_OBJ[flightDateN]) {
            TIMEBLOCKS_OBJ[flightDateN] = initDateTimeblocks()
        }

        if (timeSlotN) {
            TIMEBLOCKS_OBJ[flightDateN][timeSlotN].push({
                timeVal    ,
                flyer      ,
                id         ,
                tarifName  ,
                paymentData,
                flyerData  ,
                notes      ,

                additionalData,
            })
        }
    });

    return TIMEBLOCKS_OBJ
}

export {
    MOCK_PARCED_APIDATA,
    API_GET_INVOICES,
};
