import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

export function SLotBottomLine({expanded, setExpandAll}) {

    const expandAll = () => setExpandAll( expandAll => !expandAll)

    return (
        <div className="sLotBottomLine">
            <button
                onClick={expandAll}
            >
                {expanded ? 'collapse ' : 'expand'} all
            </button>
        </div>
    )
}